.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 16, 61, 0.84);
  z-index: 1;
}

.hidden {
  transform: translateX(100%);
  transition: transform 0.5s ease-in-out;
}

.visible {
  transform: translateX(0);
  transition: transform 0.5s ease-in-out;
}

*, *::before, *::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  color: #333;
  font-family: "Raleway", sans-serif;
  font-size: 16px;
  margin: 0;
  padding: 0;
  height: 100%;
  position: relative;
  overflow-x: hidden;
  min-height: 100vh;
  background: linear-gradient(180deg, #8bf4fd 0%, #000000 100%);
}

img {
  max-width: 100%;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
ul li {
  font-size: 16px;
}

a, li {
  text-decoration: none;
}

.heading {
  font-weight: 700;
  margin: 15px 0;
}
