.container {
  max-width: 1054px;
  margin: 0 auto;
  padding: 0 15px;
  overflow: visible;
}
.container--full {
  max-width: 1440px;
}

.grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
  justify-content: center;
  align-items: center;
  height: 100%;
}
.grid--3 {
  grid-template-columns: repeat(3, 1fr);
}
.grid--4 {
  grid-template-columns: repeat(4, 1fr);
}
.grid .flex {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.col {
  width: 100%;
  padding: 15px;
  display: inline-block;
}
@media (min-width: 992px) {
  .col--md25 {
    flex: 0 0 25%;
    max-width: 25%;
  }
}
@media (min-width: 992px) {
  .col--md33 {
    flex: 0 0 33.3%;
    max-width: 33.3%;
  }
}
@media (min-width: 992px) {
  .col--md50 {
    flex: 0 0 50%;
    max-width: 50%;
  }
}
@media (min-width: 1280px) {
