.btn {
  background-color: #5434df;
  color: #fff;
  padding: 20px 25px;
  display: inline-block;
  cursor: pointer;
  border: 2px solid transparent;
  text-align: center;
  font-size: 15px;
  font-weight: 700;
  border-radius: 15px;
  margin: 5px 5px 0 0;
  transition: background-color 0.2s ease-in-out, border 0.2s ease-in-out, color 0.2s ease-in-out;
  box-shadow: rgb(247, 241, 241) 0px 4px 20px;
}
.btn:hover {
  background-color: #fff;
  color: #5434df;
  border-color: #5434df;
}
.btn--small {
  padding: 12px 25px;
}

.btn-menu {
  display: inline-block;
  background: none;
  border: none;
  cursor: pointer;
  z-index: 10;
  border: none;
  background-color: transparent;
  margin-top: 10px;
}
.btn-menu:hover svg, .btn-menu:hover path {
  stroke: #5434df;
}
@media (min-width: 992px) {
  .btn-menu {
    display: none;
  }
}