@use '../base/variables' as *;
@use '../base/colors' as *;
@use '../base/mixins' as *;
@use '../base/utils' as *;
@use '../base/typography' as *;

.container {
  max-width: 1054px;
  margin: 0 auto;
  padding: 0 $grid-base * 1.5;
  overflow: visible;

  &--full {
    max-width: 1440px;
  }
}

.grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
    justify-content: center;
    align-items: center;
    height: 100%;

    &--3 {
        grid-template-columns: repeat(3, 1fr);
    }

    &--4 {
        grid-template-columns: repeat(4, 1fr);
}

.flex {
    display: flex;      
    justify-content: center; 
    align-items: center;           
    height: 100%;                 
}
}

.col {
    width: 100%;
    padding: $grid-base * 1.5;
    display: inline-block;

    &--md25 {
        @include medium-up {
            flex: 0 0 25%;
            max-width: 25%;
        }
    }

     &--md33 {
        @include medium-up {
            flex: 0 0 33.3%;
            max-width: 33.3%;
        }
    }

    &--md50 {
        @include medium-up {
            flex: 0 0 50%;
            max-width: 50%;
        }
    }
    &--ld25 {
        @include large-up {
            flex: 0 0 25%;
            max-width: 25%;
        }
    }

     &--ld33 {
        @include large-up {
            flex: 0 0 33.3%;
            max-width: 33.3%;
        }
    }

    &--ld50 {
        @include large-up {
            flex: 0 0 50%;
            max-width: 50%;
        }
    }

    &--hidden-sm {
        display: none;

        @include small-up {
            display: inline-block;
        }
    }
}